.navbar {
    background-color:rgb(82, 139, 158);
}

.navbar .navbar-brand a{
    color:darkmagenta;
    font-size: calc(18px + 1.3vw);
}

.navbar .navbar-brand a:hover {
    color:rgb(95, 8, 95);
}

/* .navbar-brand img {
    width: 4vw;
    height: 4vw;
} */

#navbarNav li a {
    color: rgb(72, 40, 146);
    font-size: calc(12px + 1vw);
}

#navbarNav li a:hover {
    color:rgb(240, 149, 14);
}
.hero {
    margin-bottom: calc(0px + 1vw);
    background-color: rgba(63, 46, 63, 0.8);
}

#greeting-div {
    background-color: lightblue;
    text-align: end;
}

#greeting-div h1 {
    font-size: calc(18px + 4.5vw);
}

#greeting {
    color: rgb(240, 149, 14);
    font-weight: bold;
    text-shadow: -1px -1px 0 rgb(72, 40, 146), 1px -1px 0 rgb(72, 40, 146), -1px 1px 0 rgb(72, 40, 146), 1px 1px 0 rgb(72, 40, 146);
}
.custom-footer {
    background-color: lightblue;
    font-size: calc(24px + 1.5vw);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.custom-footer .icon {
    color: rgb(72, 40, 146);
}

.custom-footer .icon:hover {
    color: rgb(240, 149, 14);
}
.project-card {
    -webkit-justify-content: center;
            justify-content: center;
    background-size: contain;
    
}
.opaq-bg {
    /* background-image: linear-gradient(rgb(255, 255, 255), rgba(255, 255, 255, .8), rgba(255, 255, 255, .6)); */
    /* background-color: #9dc5c3;
    background-image: linear-gradient(315deg, #9dc5c3 0%, #5e5c5c 74%); */

    background-color: #d9e4f53f;
    background-image: linear-gradient(315deg, #d9e4f554 0%, #f5e3e65d 74%);

    background-size: contain;
    text-align: center;
    height: 230px
}

.card-title,
.card-text,
.card-text a
{
    color: rgb(255, 255, 255);
}

.card-title:hover {
    color:indigo;
}

.card-text:hover {
    color:grey;
}

.card-text a:hover {
    color: orange
}
#contact-greeting {
    border-color:darkmagenta;
    border-width: 15px;
    border-style: solid;
    background-color:lightblue;
    color:rgb(72, 40, 146);
    font-size: 5.5vw;
    text-align:end;
}

#myInfo {
    color: slategray;
    background-color: white;
    font-size: calc(16px + .5vw);
    text-align: end;
    border-color: lightblue;
    border-width: 15px;
    border-style: solid;
    
}

#myInfo h5 {
    font-size: calc(22px + .5vw);
    font-weight: bold;
}

#myInfo a {
    color: slategray;
}

#myInfo a:hover {
    color: rgb(240, 149, 14);
    text-decoration: none;
}

/**Form**/
.form-text {
    text-align: end;
}

form label {
    font-size: 1em;
    font-weight: bolder;
}

#submit-button {
    background-color: darkmagenta;
    color: white
}

#submit-button:hover {
    color:rgb(240, 149, 14);
}

#submit-button:focus {
    color:lightblue;
}
